</template>

<script>
import Page from '../components/Pagination.vue'
import $ from 'jquery'

export default {
  components: {
    Page
  },

  data () {
    return {
      isLoading: false, // vue-loading-overlay æä»¶éé
      coupons: [], // æ¥æ¶ ajax çè¨å®åè¡¨
      tempCoupon: {}, // æ°å¢èä¿®æ¹å­å
      pagination: {}, // æ¥æ¶ ajax ç page è³æï¼è¦ props çµ¦ Pagination.vue
      modalType: 'new', // modal å¤æ·
      due_date: ''
    }
  },

  watch: {
    // ç£è½ due_date ï¼å°å¼è½çºä¸ä¸²æ¸å­ï¼å­é² tempCoupon.due_date
    due_date () {
      const timestamp = Math.floor(new Date(this.due_date) / 1000)
      this.tempCoupon.due_date = timestamp
    }
  },

  methods: {
    // æ¥æ¶ ajax çè³æï¼å­æ¾é² this.coupons
    // æ¥æ¶å­åä»¶(Pagination åé åä»¶) å³ä¾çé æ¸åæ¸
    getCoupons (page = 1) {
      this.isLoading = true
      const api = `${process.env.VUE_APP_PATH}/api/${process.env.VUE_APP_ADMIN}/admin/coupons?page=${page}`
      this.$http.get(api).then((response) => {
        console.log('åªæ å¸åè¡¨', response.data)
        this.isLoading = false
        this.coupons = response.data.coupons
        this.pagination = response.data.pagination
      })
    },

    // éå Modal (æ°å¢ãä¿®æ¹ãåªé¤)
    openModal (modalType, item) {
      const vm = this
      if (modalType === 'new') {
        vm.modalType = 'new'
        vm.tempCoupon = {}
        $('#couponModal').modal('show')
      } else if (modalType === 'edit') {
        vm.modalType = 'edit'
        vm.tempCoupon = item
        $('#couponModal').modal('show')
      } else if (modalType === 'delete') {
        vm.modalType = 'delete'
        vm.tempCoupon = item
        $('#delModal').modal('show')
      }
    },

    // æ°å¢ãä¿®æ¹ãåªé¤ï¼æä¸ç¢ºèªéµ
    updateCoupon () {
      const vm = this
      let api = ''
      let httpMethod = ''
      if (this.modalType === 'new') {
        api = `${process.env.VUE_APP_PATH}/api/${process.env.VUE_APP_ADMIN}/admin/coupon`
        httpMethod = 'post'
      } else if (this.modalType === 'edit') {
        api = `${process.env.VUE_APP_PATH}/api/${process.env.VUE_APP_ADMIN}/admin/coupon/${this.tempCoupon.id}`
        httpMethod = 'put'
      } else if (this.modalType === 'delete') {
        api = `${process.env.VUE_APP_PATH}/api/${process.env.VUE_APP_ADMIN}/admin/coupon/${this.tempCoupon.id}`
        httpMethod = 'delete'
      }

      vm.$http[httpMethod](api, { 'data': vm.tempCoupon }).then((response) => {
        console.log(this.modalType, response.data)
        this.getCoupons()
        $('#couponModal').modal('hide')
        $('#delModal').modal('hide')
      })
    }
  },

  created () {
    this.getCoupons()
  }
}
</script>
